@import "../../global.scss";

.topbar{
    width: 100%;
    height: 7%;
    color: black;
    font-weight: bold;
    position:fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;
    border-bottom-style: solid;       
    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .left{           
            display: flex;
            align-items: center;
            .itemContainer{
                display: flex;
                align-items: center;
                margin-left: 5px;
                @include mobile{ display: none; }
                span{
                    font-size: 15px;
                    font-weight: 500;                   
                    a{
                        font-size: inherit;
                        color: inherit;
                        text-decoration: none;
                        .icon{
                            font-size: 17px;
                            margin-right: 2px;
                        }
                        span{ margin-top: 2px; }
                    }
                }
            }
        }
        .right{
            .hamburger{
                    width: 32px;
                    height: 25px;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    cursor: pointer;
                    span{
                        width: 100%;
                        height: 3px;
                        background-color: black;
                        transform-origin: left;
                        transition: all 1s ease;
                    }
            }
        }
    }
    &.active{
        background-color: rgb(51, 49, 49);
        color: $mainColor;
        .hamburger{
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){ opacity: 0; }
                &:last-child{
                    background-color: white;
                    transform: rotate(-45deg);
                }               
            }
        }
    }
}